.paginationContainer{display:flex;list-style-type:none}.paginationContainer .paginationItem{padding:0 12px;height:32px;text-align:center;margin:auto 4px;color:#000;display:flex;box-sizing:border-box;align-items:center;letter-spacing:.01em;border-radius:16px;font-size:16px;min-width:32px;transition:background-color .2s ease-in-out}.paginationContainer .paginationItem.dots:hover{background-color:rgba(0,0,0,0);cursor:default}.paginationContainer .paginationItem:hover{background-color:#2d7dff;cursor:pointer}.paginationContainer .paginationItem.selected{background-color:#8ab7ff}.paginationContainer .paginationItem.selected:hover{background-color:#2d7dff}.paginationContainer .paginationItem .arrow::before{font:var(--fa-font-solid)}.paginationContainer .paginationItem .arrow.left::before{content:""}.paginationContainer .paginationItem .arrow.right::before{content:""}.paginationContainer .paginationItem.disabled{pointer-events:none}.paginationContainer .paginationItem.disabled .arrow::before{color:#a7a7a7}.paginationContainer .paginationItem.disabled:hover{background-color:rgba(0,0,0,0);cursor:default}